export default class Array {

  constructor() {


  }

	/**
	 * 
	 * @param arr : 
	 * @returns {*} : 
	 */
  arrRand(arr) {

    return arr[this.random(0, arr.length - 1)];

  }

	/**
   * 
	 * @param arr : (Array)
	 * @returns {Array}
	 */
  shuffle(arr) {

    let Arr = [];
    Arr = arr.slice();
    var i = arr.length;
    while(i){
      var j = Math.floor(Math.random()*i);
      var t = Arr[--i];
      Arr[i] = Arr[j];
      Arr[j] = t;
    }
    return Arr;

  }

}


